import { RANGE } from "constants";

const isOverMaxLength = (value, max) => {
  return value.length > max;
};

const isUnderMinLength = (value, min) => {
  return value.length < min;
};

const checkEmail = (value) => {
  if (value.includes(" ")) {
    throw new Error("   .");
  }
  if (
    isOverMaxLength(value, RANGE.EMAIL_MAX_LENGTH) ||
    isUnderMinLength(value, RANGE.EMAIL_MIN_LENGTH)
  ) {
    throw new Error(
      ` ${RANGE.EMAIL_MIN_LENGTH} , ${RANGE.EMAIL_MAX_LENGTH}  .`
    );
  }
};

export const checkNickName = (value) => {
  if (value.includes(" ")) {
    throw new Error("   .");
  }
  if (
    isOverMaxLength(value, RANGE.NICKNAME_MAX_LENGTH) ||
    isUnderMinLength(value, RANGE.NICKNAME_MIN_LENGTH)
  ) {
    throw new Error(
      ` ${RANGE.NICKNAME_MIN_LENGTH}  ${RANGE.NICKNAME_MAX_LENGTH}  .`
    );
  }
};

const checkPassword = (value) => {
  if (value.includes(" ")) {
    throw new Error("   .");
  }
  if (
    isOverMaxLength(value, RANGE.PW_MAX_LENGTH) ||
    isUnderMinLength(value, RANGE.PW_MIN_LENGTH)
  ) {
    throw new Error(
      ` ${RANGE.PW_MIN_LENGTH}  ${RANGE.PW_MAX_LENGTH}  .`
    );
  }
};

export const registerValidator = {
  email: checkEmail,
  nickname: checkNickName,
  password: checkPassword,
};
